<style lang="scss">
    .chart-platform-hd
        padding: 0 10px
    .chart-platform-loading
        padding: 20px 0
        color: #999
        font-size: 14px
        text-align: center
    .chart-platform-body
        display: flex
        height: 450px
        &.center-content
            justify-content: center
            align-items: center
        .platform-list, .coin-list
            line-height: 24px
            overflow-x: hidden
            overflow-y: auto
            &::-webkit-scrollbar
                width: 4px
                height: 4px
            &::-webkit-scrollbar-thumb
                background-color: #4c4c4c
        .platform-list
            width: 120px
            line-height: 24px
            border-right: 1px solid #4c4c4c

            li
                padding: 0 10px
                cursor: pointer
                &:hover
                    background-color: #212121
                &.active
                    background-color: #313131
        .coin-list
            width: 100%
            text-transform: uppercase
            ul
                color: #b7b7b7
                margin-bottom: 20px
                padding-top: 4px
                padding-left: 10px
                overflow: hidden
            li
                float: left
                width: 96px
                cursor: pointer
                &:hover
                    color: #fff
</style>

<template>
    <model
        ref="model"
        :open="open"
        title="平台切换"
        @close="onClose"
    >
        <div class="chart-platform-hd" slot="header">平台切换</div>
        <div v-if="list" class="chart-platform-body" slot="body">
            <div class="platform-list">
                <ul>
                    <li
                        v-for="v in list"
                        :key="v.id"
                        :class="{active: active === v.id}"
                        @click="onClick(v)"
                    >
                        {{v.exchangeName}}
                    </li>
                </ul>
            </div>
            <div class="coin-list">
                <ul
                    v-for="c in child.data"
                    :key="c.id"
                >
                    <li
                        v-for="v in c"
                        :key="v.id"
                        @click="onSelected(v)"
                    >
                        {{v.baseCurrency}}/{{v.quoteCurrency}}
                    </li>
                </ul>
            </div>
        </div>
        <div v-else class="chart-platform-body center-content">
            <div class="chart-platform-loading">数据加载中...</div>
        </div>
    </model>
</template>

<script>
    import axios from 'axios';

    import Model from './Model';


    export default {
        props: ['open'],
        components: {
            Model,
        },
        data: function() {
            return {
                isMounted: false,
                fetching: false,

                list: null,
                active: null,

                modelStyle: {
                    display: 'none',
                    visibility: 'visible',
                }
            };
        },
        beforeMount: function() {
            if (this.open) {
                this.modelStyle.display = 'block';
                this.modelStyle.visibility = 'hidden';
            }
        },
        mounted: function() {
            this.isMounted = true;

            if (this.open) {
                this.$refs.model.setCenter();
                this.fetchData();
            }
        },
        beforeDestroy: function() {
            this.isMounted = false;
        },
        computed: {
            child: function() {
                return this.list.find(c => c.id === this.active);
            }
        },
        watch: {
            open: function(n, o) {
                if (!this.list && !!n && n !== o) {
                    this.fetchData();
                }
            }
        },
        methods: {
            onClick(v) {
                this.active = v.id;
            },
            onSelected(v) {
                console.log({...v});
            },
            fetchData() {
                if (this.fetching) {
                    return;
                }

                this.fetching = true;

                return axios.get('./examples/listExchangePairInfo.json')
                    .then(response => {
                        const list = response.data.data;

                        if (list[0]) {
                            this.active = list[0].id;
                        }

                        this.list = list;
                    })
                    .finally(() => {
                        this.fetching = false;
                    });
            },
            onClose() {
                this.$emit('close');
            }
        },
    };
</script>

