<style lang="scss">
    .trade_container
        width: 250px
        height: 100%
        float: right
        z-index: 99999
        font-size: 12px
        overflow: hidden
        &.dark
            background: #0a0a0a
            color: #f1f1f1
            #orderbook
                div
                    &.table
                        div
                            &.add
                                display: none
                                background-color: rgba(238, 238, 238, 0.2)
            .trades_list
                ul
                    &.newul
                        display: none
                        background-color: rgba(238, 238, 238, 0.2)
        .green
            color: #0F0
        .red
            color: #F00
        &.light
            background: #fff
            border-left: 1px solid #afb1b3
            color: #000
            #orderbook
                border-bottom: 1px solid #afb1b3
                div
                    &.table
                        div
                            &.add
                                display: none
                                background-color: rgba(100, 100, 100, 0.2)
            .m_righttop
                position: fixed
                top: 0
                height: 40px
                line-height: 40px
                background: #FFF
                width: 230px
                border-bottom: 1px solid #afb1b3
                text-align: right
                padding-right: 20px
                em
                    background-position: 0 -32px
            #trades
                &.trades
                    table
                        color: #333
                    .v
                        color: #333
                        g
                            color: #333
            .m_rightbot
                background: #fff
                border-top: 1px solid #afb1b3
            .trades_list
                ul
                    &.newul
                        display: none
                        background-color: rgba(100, 100, 100, 0.2)
</style>

<template>
    <div class="trade_container">
        <div class="m_cent">
            <div class="m_guadan">
                <div class="symbol-title">
                    <a class="dark"></a>
                </div>
                <div id="orderbook">
                    <div id="asks">
                        <div class="table"></div>
                    </div>
                    <div id="gasks">
                        <div class="table"></div>
                    </div>
                    <div id="price" class="green"></div>
                    <div id="bids">
                        <div class="table"></div>
                    </div>
                    <div id="gbids">
                        <div class="table"></div>
                    </div>
                </div>
                <div id="trades" class="trades">
                    <div class="trades_list"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Kline from '../js/kline';

    export default {
        data: function() {
            return {
                isMounted: false,
            };
        },
        mounted: function() {
            this.isMounted = true;
        },
        beforeDestroy: function() {
            this.isMounted = false;
        },
        watch: {
            //
        },
        methods: {
            //
        },
    };
</script>

