<style lang="scss">
    .chart_container
        cursor: default
        font-family: arial, sans, serif
        font-size: 12px
        height: 100%
        position: relative
        width: 100%
        a
            &:hover
                text-decoration: none
            &.chart_icon
                border: 1px solid
                height: 16px
                padding: 0
                width: 16px
                box-sizing: border-box
                &:hover
                    border-width: 2px
        ul
            list-style: none
            border: 0
            margin: 0
            padding: 0
        button
            cursor: pointer
        // .chart_toolbar_sep
        //     float: left
        //     height: 100%
        //     width: 16px
        // .chart_toolbar_minisep
        //     float: left
        //     height: 100%
        //     width: 4px
        .chart_dropdown
            display: inline-block
            float: left
            position: relative
            z-index: 100
        .chart_dropdown_t
            background-origin: content-box
            background-repeat: no-repeat
            border: 1px solid
            border-bottom-width: 0
            margin-top: 3px
            padding-right: 10px
            z-index: 101
            position: relative
            a
                display: inline-block
                padding: 3px 12px 5px 10px
        .chart_dropdown_data
            border: 1px solid
            display: none
            position: absolute
            padding: 6px 8px 6px 8px
            margin-top: -1px
            z-index: 100
            table
                border-collapse: collapse
                font-weight: normal
                white-space: nowrap
                tr
                    &:last-child
                        td
                            border-bottom: 0
            td
                border-bottom: 1px solid
                padding: 8px 6px
                vertical-align: top
                &.marketName_
                    a
                        &.dark
                            color: #fff
                        &.light
                            color: #000
            li
                white-space: nowrap
                display: inline-block
            a
                text-decoration: none
                cursor: pointer
                padding: 5px 6px 5px 6px
                &.chart_icon
                    display: inline-block
                    margin: 0 6px 0 6px
        .chart_dropdown-hover
            &.chart_dropdown_data
                display: block

        #chart_toolbar_theme
            float: left
            padding: 0 8px
            a
                &.chart_icon
                    cursor: pointer
                    float: left
                    margin: 6px 4px
        #chart_select_theme
            td
                &:last-child
                    padding: 6px 6px 0 8px
            li
                padding: 0 4px 0 4px
        #chart_updated_time
            float: right
            margin: 4px 3px
            padding: 3px 10px

        input[type=text]
            width: 12em
    // .chart_container div,
    .chart_container ul,
    .chart_container form
        margin: 0
        padding: 0
    #chart_dom_elem_cache
        *font-weight: bold
        position: absolute
        visibility: hidden
        z-index: -1

    .market_chooser
        .chart_dropdown_data
            width: 370px
            td
                border-bottom: 1px solid
                padding: 1px 6px !important
                vertical-align: top
                line-height: 24px
        li
            float: left
            width: 80px
            height: 24px
            line-height: 24px
    #chart_dropdown_symbols
        .chart_dropdown_data
            td
                padding: 8px 6px 0 6px
            li
                display: block
                height: 26px
            a
                cursor: pointer
    #chart_dropdown_themes
        .chart_dropdown_data
            td
                &:first-child
                    padding: 6px 1px 7px 6px

    .chart_container a.chart_icon_theme_dark,
    .chart_container .chart_dropdown_data li a.chart_icon_theme_dark:hover
        background-color: #000
    .chart_container a.chart_icon_theme_light,
    .chart_container .chart_dropdown_data li a.chart_icon_theme_light:hover
        background-color: #fff

    #chart_canvasGroup
        position: absolute
        z-index: 0
    #chart_mainCanvas
        overflow: hidden
        position: absolute
        z-index: 0
    #chart_overlayCanvas
        overflow: hidden
        position: absolute
        z-index: 2
    .chart_container input,
    .chart_container button
        border-radius: 4px
        border: 1px solid
        padding: 4px
    .chart_container input[type=button],
    .chart_container input[type=submit],
    .chart_container button
        font-family: arial, sans, serif
        padding: 4px 8px
        cursor: pointer
    .chart_container.dark input,
    .chart_container.dark button
        background-color: #151515
        border-color: #333
        color: #ccc
    .chart_container.light input,
    .chart_container.light button
        background-color: #ddd
        border-color: #ddd
        color: #222
    .m_righttop
        position: fixed
        top: 0
        height: 41px
        line-height: 41px
        width: 230px
        text-align: right
        padding-right: 20px
        font-size: 16px
        color: #f78d15
        font-family: Gotham, "Helvetica Neue", Helvetica, Arial, sans-serif
        em
            width: 123px
            height: 28px
            background-position: 0 0
            display: block
            float: right
            margin-top: 5px
    .dark
        .m_righttop
            em
                background-position: 0 0
    .m_rightbot
        height: 22px
        line-height: 22px
        border-top: 1px solid #404040
        width: 230px
        text-align: right
        padding-right: 20px
        background-color: #0a0a0a
        border-bottom-color: #404040
    .m_guadan
        margin-top: 29px
        overflow: hidden
        border-left: 1px solid #404040
        border-top: 1px solid #404040
        a
            font-weight: bold
            color: #FFF
            text-decoration: none
    .light
        .m_guadan
            margin-top: 29px
            overflow: hidden
            border-left: 1px solid #afb1b3
            border-top: 1px solid #afb1b3
        .trades_list
            ul
                li
                    &.tm
                        color: #333
                    &.pr-green
                        color: #6c6
                    &.pr-red
                        color: #c66
                    &.vl
                        color: #333
                        g
                            color: #333
        .container
            span
                color: #333
            a
                text-decoration: none
                color: #1478c8
                font-family: Arial, sans, serif
    #orderbook #asks,
    #orderbook #gasks,
    #orderbook #bids,
    #orderbook #gbids
        height: 195px
        position: relative
        display: inline-block
        overflow: hidden
    .symbol-title
        font-size: 14px
        font-weight: bold
        text-align: center
        height: 16px
        line-height: 16px
        font-family: Arial, sans, serif
        padding: 5px
        .dark
            color: #6BF
        .infoDepth
            margin-left: 8px
            color: #f78d15
        a
            &:hover
                text-decoration: underline
    #asks,
    #bids
        width: 150px
    #orderbook
        padding-left: 3px
        border-bottom: 1px solid #222
        padding-bottom: 2px
        margin-left: 5px
        margin-bottom: 2px
        .table
            position: absolute
            border-collapse: collapse
            padding: 0
            margin: 0
            .row
                padding: 0
                margin: 0
                height: 13px
                line-height: 13px
                font-family: Consolas, monospace
                line-height: 13px
            .g
                color: #666
    #gasks .table,
    #asks .table
        bottom: 0
    #gasks,
    #gbids
        width: 80px
    #gasks .amount,
    #gbids .amount
        float: right
    #gasks .price,
    #gbids .price
        float: left
        text-align: right
    .price
        margin-right: 10px
        h
            visibility: hidden
    .price g,
    .amount g
        color: #666
    #price
        text-align: center
        font-size: 16px
        font-weight: bold
        height: 25px
        line-height: 25px
    .trade_container.dark #orderbook div.table div.remove g,
    .trade_container.dark #orderbook div.table div.remove span
        color: #444
    .trade_container.light #orderbook div.table div.remove g,
    .trade_container.light #orderbook div.table div.remove span
        color: #ddd
    #trades
        overflow-y: auto
        text-align: left
        color: #666
        padding-top: 5px
    .trades_list
        padding-left: 25px
        ul
            width: 200px
            height: 14px
            line-height: 14px
            text-align: left
            list-style: none
            clear: both
            zoom: 1
            margin: 0
            padding: 0
            li
                height: 14px
                line-height: 14px
                color: #999
                font-size: 12px
                list-style: none
                float: left
                *display: inline
                margin: 0
                padding: 0
                font-family: Consolas, monospace
                &.tm
                    width: 62px
                    color: #999
                &.pr-green
                    width: 65px
                    color: #6c6
                &.pr-red
                    width: 65px
                    color: #c66
                &.vl
                    width: 60px
                    color: #ccc
                    g
                        color: #666
    .container
        .nav
            margin: 0
            list-style: none
            padding: 0 0 0 3px
            height: 41px
            li
                display: inline-block
                margin-right: 9px
        a
            text-decoration: none
            color: #6BF
            font-family: Arial, sans, serif
            &:hover
                text-decoration: underline
            &.active
                color: #FC9
        span
            margin-left: 3px
            font-family: Consolas, monospace
            color: #ccc
    .chart_BoxSize
        width: 20px
        height: 20px
</style>

<template>
    <div id="kline_container">
        <trades
            v-show="tradeShown"
            :class="themeActive"
        />

        <div :class="`chart_container ${themeActive}`">
            <div id="chart_dom_elem_cache"></div>

            <tool-bar
                :ranges="ranges"
                :tabBarShown="tabBarShown"
                :toolPanelShown="toolPanelShown"
                :parameterShown="parameterShown"
                :platformShown="platformShown"
                :style="makeStyle(toolBarRect)"
                @zoom="onZoom"
                @setState="setState"
            />
            <tool-panel
                v-show="toolPanelShown"
                :style="makeStyle(toolPanelRect)"
            />

            <div
                id="chart_canvasGroup"
                class="temp"
                :style="makeStyle(canvasGroupRect)"
            >
                <canvas
                    ref="main"
                    id="chart_mainCanvas"
                    class="chart_canvas"
                    style="cursor: default;"
                />
                <canvas
                    ref="overlay"
                    id="chart_overlayCanvas"
                    class="chart_canvas"
                    style="cursor: default;"
                    @contextmenu.prevent.stop=""
                    @mousedown="onOverlayMouseDown"
                    @mousemove="onOverlayMouseMove"
                    @mouseup="onOverlayMouseUp"
                    @mouseleave="onOverlayMouseLeave"
                    @mousewheel="onOverlayMouseWheel"
                />
            </div>

            <tab-bar
                v-show="tabBarShown"
                :style="makeStyle(tabBarRect)"
            />

            <parameter-settings
                :open="parameterShown"
                @setState="setState"
            />

            <loading v-show="loading" />

            <platform
                :open="platformShown"
                @setState="setState"
                @close="setState('platformShown', false)"
            />
        </div>
    </div>
</template>

<script>
    import Kline from '../js/kline';

    import ToolBar from './ToolBar';
    import ToolPanel from './ToolPanel';
    import TabBar from './TabBar';
    import Trades from './Trades';
    import parameterSettings from './parameterSettings';
    import Loading from './Loading';
    import Platform from './Platform';

    let kline = null;

    const htmlStyle = {
        width: '100%',
        height: '100%',
        overflow: 'hidden'
    };
    const bodyStyle =  {
        position: 'fixed',
        left: '0',
        right: '0',
        top: '0',
        bottom: '0',
        width: '100%',
        height: '100%',
        zIndex: '10000'
    };

    export default {
        components: {
            ToolBar,
            ToolPanel,
            TabBar,
            Trades,
            parameterSettings,
            Loading,
            Platform,
        },
        props: {
            width: {
                type: Number,
                required: false,
                default: 1200,
            },
            height: {
                type: Number,
                required: false,
                default: 650,
            },
            theme: {
                type: String,
                required: false,
                default: 'dark',
            },
            language: {
                type: String,
                required: false,
                default: 'zh-cn',
            },
            ranges: {
                type: Array,
                required: false,
                default: function() {
                    return ["1w", "1d", "1h", "30m", "15m", "5m", "1m", "line"];
                },
            },
        },
        data: function() {
            return {
                isMounted: false,

                themeActive: null,

                loading: false,

                tradeShown: true,
                tabBarShown: true,
                toolPanelShown: false,
                parameterShown: false,
                platformShown: false,

                tabBarRect: {x: 0, y: 0, w: 0, h: 0},
                toolBarRect: {x: 0, y: 0, w: 0, h: 0},
                toolPanelRect: {x: 0, y: 0, w: 0, h: 0},
                canvasGroupRect: {x: 0, y: 0, w: 0, h: 0},
            };
        },
        created: function() {
            document.addEventListener('keyup', this.onKeyUp);
        },
        mounted: function() {
            this.isMounted = true;
            this.themeActive = this.theme;

            const kline = new Kline({
                mainCanvas: this.$refs.main,
                overlayCanvas: this.$refs.overlay,

                width: this.width,
                height: this.height,
                theme: this.theme, // light/dark
                language: this.language, // zh-cn/en-us/zh-tw
                ranges: this.ranges,
                symbol: "BTC",
                symbolName: "BTC/USD",
                type: "poll", // poll/socket
                url: "/examples/mock.json",
                limit: 1000,
                intervalTime: 5000,
                debug: false,
                showTrade: true,
            });

            kline.onRequest({beforeSend: this.onRequestbeforeSend, success: this.onRequestSuccess});
            kline.onResize(this.onResize);
            kline.onThemeChange(this.onThemeChange);
            kline.onShowTradeChange(this.onShowTrade);
            kline.draw();
            kline.pause();

            window.kline = Kline;
        },
        beforeDestroy: function() {
            this.isMounted = false;
            this.$el.removeEventListener('keyup', this.onKeyUp);
        },
        methods: {
            setState(key, value) {
                this[key] = value;
            },
            onZoom() {
                let k;
                Kline.instance.isSized = !Kline.instance.isSized;
                if (Kline.instance.isSized) {
                    for(k in bodyStyle) {
                        this.$el.style[k] = bodyStyle[k];
                    }

                    Kline.Control.onSize();

                    for(k in htmlStyle) {
                        document.body.style[k] = htmlStyle[k];
                        document.documentElement.style[k] = htmlStyle[k];
                    }
                } else {
                    for(k in bodyStyle) {
                        this.$el.style[k] = '';
                    }
                    for(k in htmlStyle) {
                        document.body.style[k] = '';
                        document.documentElement.style[k] = '';
                    }

                    Kline.Control.onSize(Kline.instance.width, Kline.instance.height);
                    this.$el.style.height = Kline.instance.height + 'px';
                }
            },
            onKeyUp(e) {
                if (e.keyCode === 46) {
                    const mgr = Kline.instance.chartMgr;
                    mgr.deleteToolObject();
                    mgr.redraw('OverlayCanvas', false);
                }
            },
            onOverlayMouseDown(e) {
                const mgr = Kline.instance.chartMgr;
                if (e.which !== 1) {
                    mgr.deleteToolObject();
                    mgr.redraw('OverlayCanvas', false);
                    return;
                }
                Kline.instance.buttonDown = true;
                let r = e.target.getBoundingClientRect();
                let x = e.clientX - r.left;
                let y = e.clientY - r.top;
                mgr.onMouseDown("frame0", x, y);
            },
            onOverlayMouseMove(e) {
                let r = e.target.getBoundingClientRect();
                let x = e.clientX - r.left;
                let y = e.clientY - r.top;
                const mgr = Kline.instance.chartMgr;
                if (Kline.instance.buttonDown === true) {
                    mgr.onMouseMove("frame0", x, y, true);
                    mgr.redraw("All", false);
                } else {
                    mgr.onMouseMove("frame0", x, y, false);
                    mgr.redraw("OverlayCanvas");
                }
            },
            onOverlayMouseUp(e) {
                if (e.which !== 1) {
                    return;
                }
                Kline.instance.buttonDown = false;
                let r = e.target.getBoundingClientRect();
                let x = e.clientX - r.left;
                let y = e.clientY - r.top;
                const mgr = Kline.instance.chartMgr;
                mgr.onMouseUp("frame0", x, y);
                mgr.redraw("All");
            },
            onOverlayMouseLeave(e) {
                let r = e.target.getBoundingClientRect();
                let x = e.clientX - r.left;
                let y = e.clientY - r.top;
                const mgr = Kline.instance.chartMgr;
                mgr.onMouseLeave("frame0", x, y, false);
                mgr.redraw("OverlayCanvas");
            },
            onOverlayMouseWheel(e) {
                if (Kline.instance.chartMgr._captureMouseWheelDirectly) {
                    Kline.Control.mouseWheel(e);
                }
            },
            makeStyle(rect) {
                return {
                    left: rect.x + 'px',
                    top: rect.y + 'px',
                    width: rect.w + 'px',
                    height: rect.h + 'px',
                }
            },
            onRequestbeforeSend() {
                this.loading = true;
            },
            onRequestSuccess() {
                this.loading = false;
            },
            onThemeChange: function(theme) {
                this.themeActive = theme;
            },
            onShowTrade(show) {
                this.tradeShown = show;
            },
            onResize(width, height) {
                this.$el.style.width = width + 'px';
                this.$el.style.height = height + 'px';

                let tabBarShown = this.tabBarShown;
                let toolPanelShown = this.toolPanelShown;
                let chartWidth = Kline.instance.showTrade ? (width - Kline.instance.tradeWidth) : width;

                this.toolBarRect.x = 0;
                this.toolBarRect.y = 0;
                this.toolBarRect.w = chartWidth;
                this.toolBarRect.h = 29;

                this.toolPanelRect.x = 0;
                this.toolPanelRect.y = this.toolBarRect.h + 1;
                this.toolPanelRect.w = toolPanelShown ? 32 : 0;
                this.toolPanelRect.h = height - this.toolPanelRect.y;

                this.tabBarRect.w = toolPanelShown ? chartWidth - (this.toolPanelRect.w + 1) : chartWidth;
                this.tabBarRect.h = tabBarShown ? 23 : 0;
                this.tabBarRect.x = chartWidth - this.tabBarRect.w;
                this.tabBarRect.y = height - (this.tabBarRect.h + 1);

                this.canvasGroupRect.x = this.tabBarRect.x;
                this.canvasGroupRect.y = this.toolPanelRect.y;
                this.canvasGroupRect.w = this.tabBarRect.w;
                this.canvasGroupRect.h = this.tabBarRect.y - this.toolPanelRect.y;

                this.$refs.main.width = this.canvasGroupRect.w;
                this.$refs.main.height = this.canvasGroupRect.h;
                this.$refs.overlay.width = this.canvasGroupRect.w;
                this.$refs.overlay.height = this.canvasGroupRect.h;
            }
        },
    };
</script>
