<style lang="scss">
    .chart_container
        #chart_tabbar
            border-top: 1px solid
            cursor: default
            *font-weight: bold
            height: 22px
            line-height: 22px
            overflow: hidden
            position: absolute
            z-index: 1
            ul
                height: 100%
                list-style: none
                padding: 0 0 0 4px
            li
                display: inline-block
                height: 100%
                margin: 0
            a
                cursor: pointer
                display: inline-block
                height: 100%
                margin: 0
                padding: 0px 4px 0 4px
                overflow: hidden
                &:hover
                    text-decoration: none
    
        &.dark
            #chart_tabbar
                background-color: #0a0a0a
                border-top-color: #404040
                a
                    color: #e5e5e5
                    &:hover
                        background-color: #383838
                        color: #fff
                    &.selected
                        color: #ffac00

        &.light
             #chart_tabbar
                background-color: #fff
                border-top-color: #afb1b3
                a
                    color: #393c40
                    &:hover
                        background-color: #f4f4f4
                        color: #393c40
                    &.selected
                        color: #f27935
</style>

<template>
    <div id="chart_tabbar">
        <ul>
            <li
                v-for="v in list"
                :key="v"
            >
                <a
                    :class="{selected: active === v}"
                    @click="onClick(v)"
                >{{v}}</a>
            </li>
        </ul>
    </div>
</template>

<script>
    import Kline from '../js/kline';

    export default {
        data: function() {
            return {
                isMounted: false,

                active: 'MACD',

                list: [
                    'MACD',
                    'KDJ',
                    'StochRSI',
                    'RSI',
                    'DMI',
                    'OBV',
                    'BOLL',
                    'SAR',
                    'DMA',
                    'TRIX',
                    'BRAR',
                    'VR',
                    'EMV',
                    'WR',
                    'ROC',
                    'MTM',
                    'PSY',
                ]
            };
        },
        mounted: function() {
            this.isMounted = true;

            Kline.on('ready', this.onReady);
        },
        beforeDestroy: function() {
            this.isMounted = false;
        },
        methods: {
            onReady() {
                const config = Kline.ChartSettings.get();
                this.active = config.charts.indics[1];
            },
            onClick(name) {
                this.active = name;
                Kline.Control.changeIndicator(name);
            }
        },
    };
</script>

